<template>
    <div class="commodityWrap">
        <section>
            <div class="menu_left">
                <ul>
                    <li v-for="(item, index) in left_menu" :key="item.id" :class="{choose_menu:isChoose[index]}" @click="changeChoose(index)">
                        <span>{{item.name}}</span>
                        <span class="category_num" v-if="leftpros[item.name] ? true : false">{{leftpros[item.name]}}</span>
                    </li>
                </ul>
            </div>
            <div class="menu_right">
                <ul>
                    <li v-for="(item, index) in left_menu" :key="item.id">
                        <div class="menu_detail_header">
                            <p>
                                <strong>{{item.name}}</strong>
                                <span>{{item.description}}</span>
                            </p>
                            <span>
                                <el-popover placement="bottom" trigger="click" :content="item.name + ' '+ item.description">
                                    <el-button slot="reference">
                                        <i class="el-icon-more"></i>
                                    </el-button>
                                </el-popover>
                            </span>
                        </div>
                        <section v-for="(products, i) in item.foods" :key="products._id" class="menu_detail_list">
                            <div>
                                <section class="menu_food_img">
                                    <img :src="'https://elm.cangdu.org/img/'+products.image_path" alt="">
                                </section>
                                <section class="menu_food_description">
                                    <h3>
                                        <strong>{{products.name}}</strong>
                                        <ul>
                                            <li v-for="(zhaopai, j) in products.attributes" :key="j" :style="{borderColor: zhaopai ? '#'+zhaopai.icon_color : 'rgba(255,255,255,0)'}">
                                                <p :style="{color:zhaopai ? '#'+zhaopai.icon_color : null}">{{zhaopai ? zhaopai.icon_name : null}}</p>
                                            </li>
                                        </ul>
                                    </h3>
                                    <p>{{products.description}}</p>
                                    <p>
                                        <span>月售{{products.month_sales}}份</span><span>好评率{{products.satisfy_rate}}%</span>
                                    </p>
                                    <p v-if="products.activity ? true : false"><span>{{products.activity ? products.activity.image_text : null}}</span></p>
                                </section>
                            </div>
                            <p>
                                <section class="food_price">
                                    <span>￥</span>
                                    <span>{{products.specfoods[0].price}}</span>
                                </section>
                                <section v-if="products.specifications.length ? true : false">
                                    <span class="show_chooselist" @click="judgeBtn(index, i, products.specfoods[0])">选规格</span>
                                    <div class="specs_cover" v-if="ishide" @click="ishide = false"></div>
                                    <div class="specs_list" v-if="index + '' + i == Specifications ? ishide : false">
                                        <h4>{{products.name}}<i class="el-icon-close close" @click="ishide = false"></i></h4>
                                        <div class="specs_details">
                                            <h5>{{products.specifications[0].name}}</h5>
                                            <ul>
                                                <li  v-for="(guige, xia) in products.specfoods" :key="guige.sku_id" @click="chooseGuige(guige, xia)" :style="{borderColor: chooseGui == xia ? '#3199e8' : '#ddd', color: chooseGui == xia ? '#3199e8' : '#333'}">{{guige.specs_name}}</li>
                                            </ul>
                                        </div>
                                        <div class="specs_footer">
                                            <div class="specs_price">
                                                <span>¥ </span>
                                                <span>{{price}}</span>
                                            </div>
                                            <div class="specs_addto_cart" @click="addCar(products.specfoods[chooseGui],index)">加入购物车</div>
                                        </div>
                                    </div>
                                </section>
                                <section class="cart_module" v-else>
                                    <i class="el-icon-remove-outline" v-if="add_car2[products.specfoods[0].name] ? true : false" @click="reducenum(products.specfoods[0].name)"></i>
                                    <span v-if="add_car2[products.specfoods[0].name] ? true : false">{{add_car2[products.specfoods[0].name]}}</span>
                                    <i class="el-icon-circle-plus" @click="addCar2(products.specfoods[0],index)"></i>
                                </section>
                            </p>
                        </section>
                    </li>
                </ul>
            </div>
        </section>
        <section class="buy_cart">
            <div @click="showCar">
                <div class="cart_icon" :style="{backgroundColor:proNum ? '#3190e8' : ''}">
                    <span class="cart_list_length" v-if="proNum ? true : false">{{proNum}}</span>
                    <i class="fa fa-shopping-cart"></i>
                </div>
                <div class="cart_num">
                    <div>¥ {{totalPrice}}</div>
                    <div>配送费¥5</div>
                </div>
            </div>
            <div :style="{backgroundColor:proPrice <= 0 ? '#4cd964' : ''}">
                <span>{{proPrice <= 0 ? '去结算' : '还差¥'+ proPrice.toFixed(2) + '起送'}}</span>
            </div>
        </section>
        <section class="cart_food_list" v-if="car.length ? isCar : false">
            <div>
                <h4>购物车</h4>
                <div class="qingkong" @click="clearCar">
                    <i class="el-icon-delete"></i>
                    <span>清空</span>
                </div>
            </div>
            <div class="cartFood">
                <ul>
                    <li v-for="(pro, j) in car" :key="j">
                        <div class="cart_list1">
                            <p class="ellipsis">{{pro.products.name}}</p>
                            <p class="ellipsis">{{pro.products.specs.length ? pro.products.specs_name : null}}</p>
                        </div>
                        <div class="cart_list2">
                            <span>¥</span>
                            <span>{{pro.products.price}}</span>
                        </div>
                        <section class="cart_module">
                            <i class="el-icon-remove-outline" @click="reducenum(pro,j)"></i>
                            <span>{{pro.count}}</span>
                            <i class="el-icon-circle-plus" @click="bottomAddcar(pro,j)"></i>
                        </section>
                    </li>
                </ul>
            </div>
        </section>
        <div class="screen_cover" @click="showCar" v-if="car.length ? ishide2 : false"></div>
    </div>
</template>

<script>
    import Vue from 'vue'
    export default {
        name: "Evaluate",
        data(){
            return {
                current_shop:null,
                left_menu:[],
                isChoose:[],
                pre_menu:0,
                ishide:false,
                Specifications:"",
                price:0,
                chooseGui:0,
                car:[],
                add_car2:{},
                isCar:false,
                ishide2:false,
                leftpros:{}
            }
        },
        created(){
            this.$parent.is_border = true;
            this.current_shop = JSON.parse(localStorage.getItem("current_shop"));
            if(localStorage.getItem("shopping_cart")){
                this.car = JSON.parse(localStorage.getItem("shopping_cart"));
                for(let j = 0; j < this.car.length; j++){
                    this.add_car2[this.car[j].products.name] = this.car[j].count;
                }
            }
            this.axios.get("/shopping/v2/menu",{
                params:{
                    restaurant_id:this.current_shop.id
                }
            }).then(res=>{
                this.left_menu = res;
                for(let i = 0; i < this.left_menu.length; i++){
                    i == 0 ? this.isChoose.push(true) : this.isChoose.push(false);
                    this.leftpros[this.left_menu[i].name] = 0;
                }
                for(let j = 0; j < this.car.length; j++){
                    this.leftpros[this.car[j].name] += this.car[j].count;
                }
            }).catch(err=>{});
        },
        computed:{
            proNum(){
                let number = 0;
                for(let i = 0; i < this.car.length; i++){
                    number += this.car[i].count;
                }
                return number;
            },
            proPrice(){
                let price = 0;
                for(let i = 0; i < this.car.length; i++){
                    price += this.car[i].products.price * this.car[i].count;
                }
                price = 20 - price;
                return price;
            },
            totalPrice(){
                let sum = 0;
                for(let i = 0; i < this.car.length; i++){
                    sum += this.car[i].products.price * this.car[i].count;
                }
                return sum.toFixed(2);
            }
        },
        methods:{
            changeChoose(i) {
                Vue.set(this.isChoose, this.pre_menu, false);
                Vue.set(this.isChoose, i ,true);
                this.pre_menu = i;
                this.$el.querySelector(".menu_right>ul>li:nth-child("+(i+1)+")").scrollIntoView({
                    behavior:"smooth",
                    block:"start"
                });
            },
            judgeBtn(m,n,specfoods) {
                this.ishide = true;
                this.Specifications = m + "" + n;
                this.price = specfoods.price;
                this.chooseGui = 0;
            },
            chooseGuige(ch,x){
                this.price = ch.price;
                this.chooseGui = x;
            },
            addCar(pro,s){
                if(!this.car.length){
                    let obj = {products:pro,count:1,name:this.left_menu[s].name};
                    this.car.push(obj);
                }else {
                    let arr = this.car.filter(v=>{
                        return v.products.specs_name == pro.specs_name;
                    });
                    let arr2 = this.car.filter(vl=>{
                        return vl.products.name == pro.name;
                    });
                    if(arr.length != 0 && arr2.length != 0){
                        arr2[0].count++;
                    }else {
                        let obj = {products:pro,count:1,name:this.left_menu[s].name};
                        this.car.push(obj);
                    }
                }
                localStorage.setItem("shopping_cart",JSON.stringify(this.car));
                this.leftpros[this.left_menu[s].name]++;
                this.ishide = false;
                this.ishide2 = false;
            },
            addCar2(pro2,s){
                if(!this.car.length){
                    let obj = {products:pro2,count:1,name:this.left_menu[s].name};
                    this.car.push(obj);
                    Vue.set(this.add_car2, pro2.name, 1);
                }else {
                    let arr = this.car.filter(v=>{
                        return v.products.name == pro2.name;
                    });
                    if(arr.length){
                        arr[0].count++;
                        this.add_car2[pro2.name]++;
                    }else {
                        let obj = {products:pro2,count:1,name:this.left_menu[s].name};
                        this.car.push(obj);
                        Vue.set(this.add_car2, pro2.name, 1);
                    }
                }
                this.ishide2 = false;
                localStorage.setItem("shopping_cart",JSON.stringify(this.car));
                this.leftpros[this.left_menu[s].name]++;
            },
            reducenum(re,du){
                if(du == undefined){
                    this.add_car2[re]--;
                    let index = this.car.findIndex(v=>{
                        return v.products.name == re;
                    });
                    this.car[index].count--;
                    this.leftpros[this.car[index].name]--;
                    if(this.add_car2[re] == 0){
                        this.car.splice(index,1)
                    }
                    localStorage.setItem("shopping_cart",JSON.stringify(this.car));
                }else {
                    this.car[du].count--;
                    this.add_car2[re.products.name]--;
                    this.leftpros[re.name]--;
                    if(this.car[du].count == 0){
                        this.car.splice(du,1);
                    }
                    localStorage.setItem("shopping_cart",JSON.stringify(this.car));
                }
                if(this.car.length == 0){
                    this.isCar = false;
                }
            },
            clearCar(){
                this.isCar = false;
                localStorage.removeItem("shopping_cart");
                this.car.splice(0, this.car.length);
                for(let key in this.add_car2){
                    delete this.add_car2[key];
                }
                for(let k in this.leftpros){
                    this.leftpros[k] = 0;
                }
            },
            showCar(){
                $(".cart_food_list").slideToggle(500);
                if(this.car.length){
                    this.ishide2 = !this.ishide2;
                    this.isCar = true;
                }else {
                    this.isCar = false;
                }
            },
            bottomAddcar(pros,x){
                this.leftpros[pros.name]++;
                if(pros.products.specs.length){
                    this.car[x].count++;
                }else {
                    this.car[x].count++;
                    this.add_car2[pros.products.name]++;
                }
                localStorage.setItem("shopping_cart",JSON.stringify(this.car));
            }
        }
    }
</script>

